.add-product-layout {
	height: 100%;
	max-height: 100%;
	background-color: #f8f8f8;
	display: grid;
	grid-auto-columns: 1fr;
	grid-auto-rows: 1fr;
	grid-template-columns: 0.1fr 0.5fr repeat(2, 0.1fr 1fr) 0.1fr;
	grid-template-rows: 0.3fr 0.1fr 0.3fr 1fr repeat(2, 0.1fr 0.5fr) 0.2fr;
	gap: 0px 0px;
	grid-template-areas:
		"headerBar-container headerBar-container headerBar-container headerBar-container headerBar-container headerBar-container headerBar-container"
		". . . . . . ."
		". navBar-container . form-container form-container form-container ."
		". navBar-container . form-container form-container form-container ."
		". navBar-container . form-container form-container form-container ."
		". navBar-container . form-container form-container form-container ."
		". navBar-container . form-container form-container form-container ."
		". navBar-container . form-container form-container form-container ."
		". . . . . . .";
}


.form-container {
	grid-area: form-container;
	height: 100vh;
	max-height: 100vh;
	background-color: #f8f8f8;
	display: grid;
	grid-auto-columns: 1fr;
	grid-auto-rows: 1fr;
	grid-template-columns: repeat(2, 0.1fr 2fr) 0.1fr;
	grid-template-rows: 0.3fr 1fr repeat(2, 0.1fr 0.5fr) 0.2fr;
	gap: 0px 0px;
	grid-template-areas:
		" . add-product-title-container add-product-title-container add-product-title-container ."
		" . add-product-info-container add-product-info-container add-product-info-container ."
		" . . . . ."
		" . add-product-char-container . add-product-stock-container ."
		" . add-product-char-container . . ."
		" . add-product-char-container . add-product-price-container ."
		" . . . . .";
}

.add-product-layout h2 {
	margin: 0;
	padding: 0;
}