.reports-month-button-container {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	height: 4.5625rem;
	background-color: #222222;
	border-radius: 1rem;
}

.reports-month-button {
	width: 10%;
	border-radius: 1rem;
	background-color: transparent;
	border: none;
}

.reports-month-button-text {
	font-size: 1.5rem;
	color: white;
	text-transform: capitalize;
}

@media (max-width: 900px) {
	.reports-month-button-container {
		margin-bottom: 1.7rem;
	}

	.reports-month-button-text {
		font-size: 1rem;
	}
}